@import url("https://fonts.googleapis.com/css2?family=Archivo:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Archivo:wght@100..900&family=Bai+Jamjuree:wght@200;300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body div {
  display: flex;
}
body header {
  margin: 3rem;
  height: 100%;
  height: 4rem;
  display: flex;
  font-family: "Bai Jamjuree", sans-serif;
  font-weight: 400;
  padding-left: 3rem;
}
body header p {
  display: flex;
  align-items: center;
  font-size: 3rem;
  font-weight: 600;
}
body header p span {
  font-weight: 500;
  font-size: 5rem;
}
body nav {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 3rem;
  width: 100%;
}
body nav a {
  text-decoration: none;
  color: black;
  font-size: 2.5rem;
  font-weight: 600;
  position: relative;
}
body nav a:hover:after {
  width: 100%;
}
body nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 5%;
  background-color: black;
  transition: width 0.4s ease;
}
body main .conteiner {
  display: flex;
}
body main .conteiner .texto {
  padding-top: 3rem;
  margin: 3em;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
body main .conteiner .texto h1 {
  font-size: 3.8rem;
  text-align: justify;
  font-family: "Bai Jamjuree", sans-serif;
  margin: 3rem;
  width: 65rem;
}
body main .conteiner .texto button {
  margin-left: 3rem;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 5rem;
  border-radius: 5rem;
  background-color: #F9A826;
  transition: background-color 0.3s ease;
}
body main .conteiner .texto button:hover {
  background-color: #be8324;
}
body main .conteiner .texto button a {
  text-decoration: none;
  font-size: 2.5rem;
  color: white;
  font-family: "Archivo", sans-serif;
}
body main .conteiner .imagembitcoin {
  padding-top: 6rem;
  display: flex;
  justify-content: end;
}